:root {
  --main-ff: "Montserrat", sans-serif;
  --color-dark: #2D2E33;
  --color-white: #fff;
  --color-green: #52BA61;
  --main-transition: 200ms;
}

@font-face {
  font-weight: 400;
  font-family: "Montserrat";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}

@font-face {
  font-weight: 500;
  font-family: "Montserrat";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
}

@font-face {
  font-weight: 600;
  font-family: "Montserrat";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}

.button-rest {
  margin: 0;
  padding: 0;
  background-color: transparent;
  border-radius: 0;
  border: none;
  font-family: var(--main-ff);
}

.button {
  display: inline-flex;
  align-items: center;
  padding: 0.8rem 1rem;
  border-radius: 2rem;
  border: 2px solid var(--color-green);
  color: var(--color-green);
  font-weight: 500;
  font-size: 2rem;
  line-height: 2.4rem;
  background-color: var(--color-white);
  text-transform: uppercase;
  transition: color var(--main-transition), background-color var(--main-transition);
  cursor: pointer;
}

.button__icon {
  display: block;
  margin-left: 0.7rem;
  fill: var(--color-green);
  transition: fill var(--main-transition);
}

@media screen and (max-width: 480px) {
  .button__icon {
    width: 2rem;
    height: 2rem;
  }
}

.button:hover {
  background-color: var(--color-green);
  color: var(--color-white);
}

.button:hover .button__icon {
  fill: var(--color-white);
}

.button_green {
  background-color: var(--color-green);
  color: var(--color-white);
}

.button_green .button__icon {
  fill: var(--color-white);
}

.button_green:hover {
  background-color: var(--color-white);
  color: var(--color-green);
}

.button_green:hover .button__icon {
  fill: var(--color-green);
}

@media screen and (max-width: 767px) {
  .button {
    font-size: 1.8rem;
    line-height: 2rem;
  }
}

@media screen and (max-width: 480px) {
  .button {
    font-size: 1.6rem;
    line-height: 1.8rem;
  }
}

.button-burger {
  position: relative;
  width: 4rem;
  height: 4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.button-burger span {
  display: block;
  flex-shrink: 0;
  width: 100%;
  height: 0.3rem;
  background-color: var(--color-white);
  transition: all 500ms;
  pointer-events: none;
}

.button-burger span:nth-child(2) {
  margin: 5px 0;
}

.button-burger_is-active span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.button-burger_is-active span:nth-child(2) {
  opacity: 0;
}

.button-burger_is-active span:nth-child(1) {
  transform: translate(-50%, -50%) rotate(45deg);
}

.button-burger_is-active span:nth-child(3) {
  transform: translate(-50%, -50%) rotate(-45deg);
}

.logo {
  display: inline-block;
}

.logo__img {
  max-width: 19.1rem;
}

@media screen and (max-width: 767px) {
  .logo__img {
    max-width: 11.1rem;
  }
}

.nav__list {
  display: flex;
}

.nav__item {
  position: relative;
  margin-right: 4rem;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  position: relative;
  font-weight: 500;
  font-size: 1.6rem;
  line-height: 2rem;
  text-transform: uppercase;
  color: var(--color-white);
  transition: color var(--main-transition);
}

.nav__link:hover {
  color: var(--color-green);
}

.hero-arrow-down {
  fill: var(--color-white);
}

.site-middle-text {
  font-weight: 500;
  font-size: 2.5rem;
  line-height: 3rem;
  text-transform: uppercase;
}

@media screen and (max-width: 991px) {
  .site-middle-text {
    font-size: 2rem;
    line-height: 2.6rem;
  }
}

@media screen and (max-width: 767px) {
  .site-middle-text {
    font-size: 1.5rem;
    line-height: 1.8rem;
  }
}

.section-title {
  font-size: 11rem;
  line-height: 1;
  text-transform: uppercase;
}

@media screen and (max-width: 1280px) {
  .section-title {
    font-size: 10rem;
  }
}

@media screen and (max-width: 991px) {
  .section-title {
    font-size: 8rem;
  }
}

@media screen and (max-width: 767px) {
  .section-title {
    font-size: 4rem;
  }
}

.section-name {
  position: relative;
  display: inline-block;
  font-weight: 600;
  font-size: 1.4rem;
  line-height: 1.7rem;
  padding-left: 2rem;
}

.section-name::before {
  position: absolute;
  top: 0.3rem;
  left: 0;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: currentColor;
  content: "";
}

.markweber {
  margin-left: auto;
  transition: color var(--main-transition);
}

.markweber:hover {
  color: var(--color-green);
}

@media screen and (max-width: 575px) {
  .markweber {
    margin-left: 0;
    text-decoration: underline;
  }
}

.preloader {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 30;
  width: 100%;
  height: 100%;
  background-color: var(--color-dark);
  transition: opacity var(--main-transition), visibility var(--main-transition);
}

.preloader_is-loaded {
  opacity: 0;
  visibility: hidden;
}

.drop-menu-container {
  position: relative;
  overflow: hidden;
  transition: max-height var(--main-transition);
  max-height: 0;
}

.site-hero-text {
  font-weight: 500;
  font-size: 6rem;
  line-height: 6.6rem;
  text-transform: uppercase;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-size: 10px;
  line-height: 1.3;
}

@media screen and (min-width: 2100px) {
  html {
    font-size: 14px;
  }
}

@media screen and (max-width: 1536px) {
  html {
    font-size: 8px;
  }
}

@media screen and (max-width: 1366px) {
  html {
    font-size: 7px;
  }
}

@media screen and (max-width: 767px) {
  html {
    font-size: 10px;
  }
}

@media screen and (max-width: 320px) {
  html {
    font-size: 9px;
  }
}

body {
  color: var(--color-dark);
  font-weight: 400;
  font-size: 1.5rem;
  line-height: 2rem;
  font-family: var(--main-ff);
  background-color: var(--color-white);
}

body ::selection {
  background-color: var(--color-dark);
  color: var(--color-white);
}

@media screen and (max-width: 1536px) {
  body {
    font-size: 1.6rem;
    line-height: 2rem;
  }
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

a {
  color: inherit;
  text-decoration: none;
}

.list-rest {
  padding: 0;
  margin: 0;
  list-style: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}

.site-container {
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  padding: 0 40px;
}

@media screen and (max-width: 991px) {
  .site-container {
    padding: 0 20px;
  }
}

@media screen and (max-width: 360px) {
  .site-container {
    padding: 0 16px;
  }
}

.page-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden;
}

.page-main {
  flex-grow: 1;
}

.site-content-grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 20px;
}

.site-content-grid__block:nth-of-type(1) {
  grid-column: 4 span;
}

.site-content-grid__block:nth-of-type(2) {
  grid-column: 8 span;
}

