  padding-top: 3.5rem;
}

.header__nav-block {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  column-gap: 20px;
}

.header__nav-block-item:nth-child(1) {
  grid-column: 4 span;
}

.header__nav-block-item:nth-child(2) {
  grid-column: 2 span;
}

.header__nav-block-item:nth-child(3) {
  grid-column: 1 span;
}

.header__nav-block-item:nth-child(4) {
  grid-column: 1 span;
  text-align: right;
}

@media screen and (max-width: 991px) {
  .header__nav-block-item {
    display: none;
  }
}

.header__nav-block-item._mobile {
  display: none;
}

@media screen and (max-width: 991px) {
  .header__nav-block-item._mobile {
    display: block;
    grid-column: 8 span;
  }
}

.header__button-burger {
  margin-left: auto;
}

.header__link {
